import React, { useState, useContext, useCallback, useMemo, useEffect } from 'react'
import { useSelector } from 'react-redux'
import { useWeb3React } from '@web3-react/core'
import { Collapse } from 'react-collapse'
import Flex from 'components/Flex'
import CheckBox from 'components/CheckBox'
import Text from 'components/Text'
import Box from 'components/Box'

import get from 'lodash/get'
import styled from 'styled-components'
import SearchInput from 'components/SearchInput'

import DropDownFilter from '../DropDownFilter'

const Content = styled(Box)`
  position: relative;
  margin-top: 25px;
  width: 100%;
`
const Label = styled.label`
  // margin-top: 3px;
  margin: 4px;
  font-style: normal;
  font-weight: bold;
  font-size: 1.3em;

  color: #e0e0e0;
`

const SeacchCybloc = ({ setQuery, query }) => {
  const [searchId, setSearchId] = useState('')
  const { account } = useWeb3React()
  const { cyblocId } = query
  const [isOpen, setIsOpen] = useState(true)

  useEffect(() => {
    if (cyblocId) {
      setSearchId(cyblocId)
    } else {
      setSearchId('')
    }
  }, [cyblocId])

  const onHandleClickSearch = () => {
    // Change filter info here
    setQuery({
      cyblocId: searchId,
      pageNumber: 1,
    })
  }

  const onChange = (e) => {
    const newVal = get(e, 'target.value', null)
    setSearchId(newVal)
  }

  const onKeyDown = (e) => {
    // User pressed the enter key
    if (e.keyCode === 13) {
      onHandleClickSearch()
    }
  }
  const onChangeState = () => {
    setIsOpen(!isOpen)
  }

  return (
    <Content>
      <SearchInput
        onHandleClickSearch={onHandleClickSearch}
        onChange={onChange}
        type="number"
        placeholder="CyBloc id"
        onKeyDown={onKeyDown}
        value={searchId}
        style={{
          maxWidth: 'none',
          flex: 1,
          // height: '39px'
        }}
      />
    </Content>
  )
}
export default SeacchCybloc
