import React from 'react'
import { Switch, Route, useRouteMatch } from 'react-router-dom'
import GalleryRoute from './Gallery'

const Gallery = () => {
  const { path } = useRouteMatch()
  return (
    <Switch>
      <Route path={`${path}`}>
        <GalleryRoute />
      </Route>
    </Switch>
  )
}

export default Gallery
