import React, { useState, useEffect } from 'react'
import styled from 'styled-components'
import { useSelector } from 'react-redux'
import queryString from 'query-string'
import { useWeb3React } from '@web3-react/core'
import Text from 'components/Text'
import Flex from 'components/Flex'
import Box from 'components/Box'
import LoaderIcon from 'components/LoaderIcon'
import { toast } from 'react-toastify'
import { useAppDispatch } from 'state'
import { selectContractAddress } from 'state/reducers/app'
import differenceWith from 'lodash/differenceWith'
import isEqual from 'lodash/isEqual'
import useOpenPackage from 'hooks/useOpenPackage'
import useCanUnpack from 'hooks/useCanUnpack'
import useWeb3 from 'hooks/useWeb3'
import { selectCyblocList, setCyblocList, setNewCylocList } from 'state/reducers/cybloc'
import { getCyblocList } from 'pages/Inventory/hooks/useGetCyblocList'
import { NUMBER_OF_PACKAGE_OPEN } from 'config/constant'
import { retryPromiseWithDelay } from 'utils/promise'
import Modal from './BaseModal'

const Wrapper = styled(Box)`
  text-align: center;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  height: 100%;
`
const WalletButton = styled.button`
  justify-content: center;
  display: flex;
  background-color: transparent;
  border: none;
  flex-direction: column;
  align-items: center;
  background-image: url('/images/login-button-background.png');
  background-repeat: no-repeat;
  background-size: contain;
  background-position: center;
  width: 280px;
  transition: background-image 0.3s ease-in, transform 0.1s ease-in;
  padding: 13px 0px;
  border-radius: 2px;

  &:not(:last-of-type) {
    margin-bottom: 20px;
  }

  &:disabled {
    opacity: 0.2;
  }

  &:hover:disabled {
    cursor: not-allowed;
  }

  &:hover:enabled {
    cursor: pointer;
    background-image: url('/images/login-button-background-hover.png');
  }

  &:active:enabled {
    transform: translateY(-2px);
    opacity: 80%;
  }

  & > div {
    color: #fefefe;
    line-height: 17.15px;
    letter-spacing: 2px;
    font-weight: 700;
  }
`

const StyledSubText = styled(Text)`
  font-size: 1.1rem;
  color: #ffffff;
  line-height: 20px;
  letter-spacing: 2px;
`
const StyledHeaderText = styled(Text)`
  color: #f3f3f3;
  font-size: 2rem;
  padding: 0 60px;
  margin-bottom: 10px;

  @media (max-width: 576px) {
    padding: 0px;
  }
`

const NotificationText = styled(Text)`
  color: red;
  font-size: 1rem;
  padding: 0 60px;
  margin-bottom: 25px;

  @media (max-width: 576px) {
    padding: 0px;
  }
`

const getNPackIndex = (packs, limit) => {
  const result = []

  for (let i = 0; i < limit; i += 1) {
    if (packs[i]) {
      result.push(packs[i])
    } else {
      break
    }
  }

  return result
}

const getTokenIdFromPackInfo = (packInfo) => {
  if (packInfo && packInfo.length > 0) {
    const mappedData = packInfo.map((pack) => {
      return pack.tokenId
    })
    return getNPackIndex(mappedData, NUMBER_OF_PACKAGE_OPEN)
  }

  return []
}

const OpenPackModal = ({ packageData, onDismiss, history, path }) => {
  const { commonPackageData, rarePackageData, epicPackageData } = packageData
  const {
    CYBLOC_PACK: { ADDRESS: PACK_ADDRESS },
    CYBLOC_UTILS: { ADDRESS: UTIL_ADDRESS },
  } = useSelector(selectContractAddress)
  const dispatch = useAppDispatch()
  const [isLoadingCommon, setIsLoadingCommon] = useState(false)
  const { account } = useWeb3React()
  const [isLoadingBlockNumber, setIsLoadingBlockNumber] = useState(true)
  const [currentBlock, setCurrentBlock] = useState(0)
  const web3 = useWeb3()
  const [isLoadingRare, setIsLoadingRare] = useState(false)
  const [isLoadingEpic, setIsLoadingEpic] = useState(false)
  const { onUnpack } = useOpenPackage(PACK_ADDRESS)
  const { canUnpack } = useCanUnpack(UTIL_ADDRESS, PACK_ADDRESS)
  const cyblocList = useSelector(selectCyblocList)

  useEffect(() => {
    const interval = setInterval(async () => {
      const blockNumber = await web3.eth.getBlockNumber()
      setCurrentBlock(blockNumber)
      setIsLoadingBlockNumber(false)
    }, 2000)

    return () => clearInterval(interval)
  }, [web3])

  const onHandleOpenPackage = async (packageType) => {
    try {
      let packageIndex = null
      let tokenIdList = null
      if (packageType === '1') {
        setIsLoadingCommon(true)
        packageIndex = getNPackIndex(commonPackageData, NUMBER_OF_PACKAGE_OPEN)
        tokenIdList = getTokenIdFromPackInfo(commonPackageData)
      } else if (packageType === '2') {
        setIsLoadingRare(true)
        packageIndex = getNPackIndex(rarePackageData, NUMBER_OF_PACKAGE_OPEN)
        tokenIdList = getTokenIdFromPackInfo(rarePackageData)
      } else {
        setIsLoadingEpic(true)
        packageIndex = getNPackIndex(epicPackageData, NUMBER_OF_PACKAGE_OPEN)
        tokenIdList = getTokenIdFromPackInfo(epicPackageData)
      }
      const isLocked = await canUnpack(packageIndex)
      const result = await onUnpack(packageIndex, isLocked)
      const data = await retryPromiseWithDelay(getCyblocList(account, currentBlock), 20, 2000)
      if (data) {
        const { cyblocs } = data
        dispatch(setNewCylocList(cyblocs))
        setIsLoadingEpic(false)
        setIsLoadingRare(false)
        setIsLoadingCommon(false)
        // Query to push to URL
        const stringtifiedQuery = queryString.stringify(
          {
            packageIndex: tokenIdList,
          },
          {
            arrayFormat: 'bracket',
          },
        )
        history.push(`${path}/package-opening/${packageType}?${stringtifiedQuery}`)
      }
      onDismiss()
    } catch (error) {
      console.log(error, 'error ?')
      setIsLoadingRare(false)
      setIsLoadingEpic(false)
      setIsLoadingCommon(false)
      if (error && error.code === 4001) {
        return
      }
      toast.error('Fail to open package', {
        hideProgressBar: true,
      })
    }
  }

  return (
    <Modal title="Your wallet">
      <Wrapper>
        {isLoadingBlockNumber ? (
          <LoaderIcon />
        ) : (
          <>
            <Box>
              <StyledHeaderText bold>Choose a pack type you want to open</StyledHeaderText>
            </Box>
            <Box>
              <NotificationText>
                You can only open a maximum of {NUMBER_OF_PACKAGE_OPEN} packages at a time
              </NotificationText>
            </Box>
            <Box mb="15px">
              <WalletButton
                disabled={commonPackageData.length === 0 || isLoadingEpic || isLoadingRare}
                onClick={() => {
                  onHandleOpenPackage('1')
                }}
              >
                {isLoadingCommon ? (
                  <LoaderIcon />
                ) : (
                  <>
                    <Text mr="5px" fontSize="1.6rem">
                      Common packs
                    </Text>
                    <StyledSubText mr="5px">
                      {commonPackageData.length} available {commonPackageData.length > 1 ? 'packs' : 'pack'}
                    </StyledSubText>
                  </>
                )}
              </WalletButton>
              <WalletButton
                disabled={rarePackageData.length === 0 || isLoadingCommon || isLoadingEpic}
                onClick={() => {
                  onHandleOpenPackage('2')
                }}
              >
                {isLoadingRare ? (
                  <LoaderIcon />
                ) : (
                  <>
                    <Text mr="5px" fontSize="1.6rem">
                      Rare packs
                    </Text>
                    <StyledSubText mr="5px">
                      {rarePackageData.length} available {rarePackageData.length > 1 ? 'packs' : 'pack'}
                    </StyledSubText>
                  </>
                )}
              </WalletButton>
              <WalletButton
                disabled={epicPackageData.length === 0 || isLoadingCommon || isLoadingRare}
                onClick={() => {
                  onHandleOpenPackage('3')
                }}
              >
                {isLoadingEpic ? (
                  <LoaderIcon />
                ) : (
                  <>
                    <Text mr="5px" fontSize="1.6rem">
                      Epic packs
                    </Text>
                    <StyledSubText mr="5px">
                      {epicPackageData.length} available {epicPackageData.length > 1 ? 'packs' : 'pack'}
                    </StyledSubText>
                  </>
                )}
              </WalletButton>
            </Box>
          </>
        )}
      </Wrapper>
    </Modal>
  )
}

export default OpenPackModal
