import React, { useState, useContext, useCallback, useMemo } from 'react'
import { useSelector } from 'react-redux'
import { Collapse } from 'react-collapse'
import styled from 'styled-components'
import get from 'lodash/get'
import Flex from 'components/Flex'
import Box from 'components/Box'
import Text from 'components/Text'
import Autocomplete from 'components/Autocomplete'
import { TRAIT_TYPE } from 'config/constant'
import { selectTraitConfig } from 'state/reducers/app'
import DropDownFilter from '../DropDownFilter'
import SearchInput from '../../../../components/SearchInput'

const Content = styled(Flex)`
  padding: 45px 0px 23px 24px;
  flex-direction: column;
`
const Button = styled.button`
  position: relative;
  width: 100%;
  height: 48px;
  margin-top: 10px;
  justify-content: center;
  align-items: center;
  display: flex;
  background-color: transparent;
  border: none;
  cursor: pointer;
`

const FilterButton = styled(Button)`
  margin-top: 2rem;
  background-image: ${(props) =>
    props.selected ? "url('/images/BgFilterButtonSeleted.png')" : "url('/images/BgFilterButton.png')"};
  background-repeat: no-repeat;
  background-size: contain;
  background-position: center;
  // transition: background-image 0.3s ease-in, transform 0.1s ease-in, opacity: 0.3s ease-in;

  &:hover {
    // opacity: 1;
  }
  & > div {
    opacity: ${(props) => (props.selected ? '1' : '0.5')};
  }
  & > img {
    opacity: ${(props) => (props.selected ? '1' : '0.5')};
  }
  z-index: 99;
`

const IconImage = styled.img`
  width: 2.9rem;
  height: 2.9rem;
  object-fit: contain;
  position: absolute;
  right: 9px;
  top: 9px;
`
const Title = styled(Text)`
  font-size: 1.6rem;
  color: #e0e0e0;
  line-height: 17px;
  letter-spacing: 2px;
  font-weight: bold;
`
const WrapperListContent = styled(Box)`
  width: 100%;
  max-height: 400px;
  padding-right: 20px;
  overflow-y: scroll;
`

const TraitFilter = ({ setQuery, query }) => {
  const [isOpen, setIsOpen] = useState(false)
  const [searchValue, setSearchValue] = useState('')
  const traits = useSelector(selectTraitConfig)
  const TRAITS_TYPES_CONFIG = useMemo(() => {
    return get(traits, 'traitTypes', [])
  }, [traits])

  const TRAITS_CONFIG = useMemo(() => {
    return get(traits, 'traits', [])
  }, [traits])

  const { traitType, traitId } = query

  const traitFilter = useMemo(() => {
    if (traitType && traitType.length !== 0) {
      return traitType
    }

    return traitId
  }, [traitType, traitId])

  const onHandleSelectType = (tid, type) => {
    let newTraitFilter

    const found = traitFilter.find((el) => Number(el) === tid)
    if (found) {
      newTraitFilter = traitFilter.filter((el) => Number(el) !== tid)
    } else {
      newTraitFilter = [tid]
    }

    if (type === TRAIT_TYPE.NAME) {
      setQuery({
        traitType: newTraitFilter,
        traitId: undefined,
        pageNumber: 1,
      })
    } else {
      setQuery({
        traitId: newTraitFilter,
        traitType: undefined,
        pageNumber: 1,
      })
    }
  }

  const onChangeState = () => {
    // setIsOpen(!isOpen)
  }
  return (
    <DropDownFilter mt="46px" isOpen={isOpen} title="Trait" onChangeState={onChangeState}>
      <Collapse isOpened={isOpen}>
        <Content>
          <Autocomplete
            onSearch={() => {
              const foundTrait = TRAITS_CONFIG.find((el) => el.name === searchValue)
              if (foundTrait) {
                onHandleSelectType(foundTrait.id, TRAIT_TYPE.ID)
              }
            }}
            suggestions={TRAITS_CONFIG.map((trait) => trait.name)}
            value={searchValue}
            onChangeValue={(val) => {
              setSearchValue(val)
            }}
          />
          <WrapperListContent>
            {TRAITS_TYPES_CONFIG.map((trait) => (
              <FilterButton
                key={trait.id}
                selected={traitFilter.map((t) => Number(t)).includes(trait.id) && traitType && traitType.length !== 0}
                onClick={() => {
                  onHandleSelectType(trait.id, TRAIT_TYPE.NAME)
                }}
              >
                <Title>{trait.name}</Title>
              </FilterButton>
            ))}
          </WrapperListContent>
        </Content>
      </Collapse>
    </DropDownFilter>
  )
}
export default TraitFilter
