import React, { useContext, useEffect, useState } from 'react'
import { useSelector } from 'react-redux'
import { Collapse } from 'react-collapse'
import styled from 'styled-components'
import Flex from 'components/Flex'
import Box from 'components/Box'
import Text from 'components/Text'
import DropDownFilter from '../DropDownFilter'
import SkillSlider from './utils/SkillSlider'

const Content = styled(Flex)`
  padding: 45px 24px 10px 24px;
  flex-direction: column;
  transition: height 500ms;
`
const Test = styled(Flex)`
  width: 200px;
  height: 100px;
`
export const Button = styled(Box)`
  display: flex;
  justify-content: center;
  margin: 10px 0px;
  align-items: flex-start;
  background-image: url('/images/login-button-background.png');
  background-repeat: no-repeat;
  background-size: contain;
  background-position: center;
  width: 280px;
  transition: background-image 0.3s ease-in, transform 0.1s ease-in;
  padding: 15px 10px;
  z-index: 999;
  border-radius: 2px;

  &:not(:last-of-type) {
    margin-bottom: 20px;
  }

  &:hover {
    cursor: pointer;
    background-image: url('/images/login-button-background-hover.png');
  }

  &:active {
    transform: translateY(-2px);
    opacity: 80%;
  }

  & > div {
    font-size: 1.6rem;
    color: #fefefe;
    font-weight: 700;
  }
`
export const BaseText = styled(Text)`
  font-weight: 500;
  color: #ffffff;
`

const SkillFilter = ({ setQuery, query }) => {
  const [isOpen, setIsOpen] = useState(false)
  const DEFAULT = {
    min: 0,
    max: 100,
  }
  const {
    shootingMin = 0,
    shootingMax = 100,
    crossingMin = 0,
    crossingMax = 100,
    tacklingMin = 0,
    tacklingMax = 100,
    passingMin = 0,
    passingMax = 100,
    dribblingMin = 0,
    dribblingMax = 100,
    physicalMin = 0,
    physicalMax = 100,
  } = query
  const [shootingFilter, setShootingFilter] = useState({
    min: shootingMin,
    max: shootingMax,
  })
  const [crossingFilter, setCrossingFilter] = useState({
    min: crossingMin,
    max: crossingMax,
  })
  const [tacklingFilter, setTacklingFilter] = useState({
    min: tacklingMin,
    max: tacklingMax,
  })
  const [passingFilter, setPassingFilter] = useState({
    min: passingMin,
    max: passingMax,
  })
  const [dribblingFilter, setDribblingFilter] = useState({
    min: dribblingMin,
    max: dribblingMax,
  })
  const [physicalFilter, setPhysicalFilter] = useState({
    min: physicalMin,
    max: physicalMax,
  })

  const onChangeState = () => {
    // setIsOpen(!isOpen)
  }

  const onHandleFilterSkill = () => {
    setQuery({
      shootingMin: shootingFilter.min,
      shootingMax: shootingFilter.max,
      crossingMin: crossingFilter.min,
      crossingMax: crossingFilter.max,
      tacklingMin: tacklingFilter.min,
      tacklingMax: tacklingFilter.max,
      dribblingMin: dribblingFilter.min,
      dribblingMax: dribblingFilter.max,
      physicalMin: physicalFilter.min,
      physicalMax: physicalFilter.max,
      passingMin: passingFilter.min,
      passingMax: passingFilter.max,
      pageNumber: 1,
    })
  }

  return (
    <DropDownFilter mt="30px" isOpen={isOpen} title="Skill" onChangeState={onChangeState}>
      <Collapse isOpened={isOpen}>
        <Content>
          <SkillSlider
            title="Shooting"
            rangeValue={shootingFilter}
            handleOnChange={(value) => {
              setShootingFilter(value)
            }}
          />
          <SkillSlider
            title="Crossing"
            rangeValue={crossingFilter}
            handleOnChange={(value) => {
              setCrossingFilter(value)
            }}
          />
          <SkillSlider
            title="Tackling"
            rangeValue={tacklingFilter}
            handleOnChange={(value) => {
              setTacklingFilter(value)
            }}
          />
          <SkillSlider
            title="Passing"
            rangeValue={passingFilter}
            handleOnChange={(value) => {
              setPassingFilter(value)
            }}
          />
          <SkillSlider
            rangeValue={dribblingFilter}
            title="Dribbling"
            handleOnChange={(value) => {
              setDribblingFilter(value)
            }}
          />
          <SkillSlider
            rangeValue={physicalFilter}
            title="Physical"
            handleOnChange={(value) => {
              setPhysicalFilter(value)
            }}
          />
          <Flex justifyContent="center">
            <Button onClick={onHandleFilterSkill}>
              <BaseText fontSize="2rem">APPLY</BaseText>
            </Button>
          </Flex>
        </Content>
      </Collapse>
    </DropDownFilter>
  )
}
export default SkillFilter
