import React, { useEffect, useState, useMemo } from 'react'
import Skeleton from 'react-loading-skeleton'
import { useSelector } from 'react-redux'
import styled from 'styled-components'
import get from 'lodash/get'
import find from 'lodash/find'
import Flex from 'components/Flex'
import Text from 'components/Text'
import { selectTraitConfig, selectTypeConfig } from 'state/reducers/app'
import { TRAIT_TYPE } from 'config/constant/index.js'

import SortFilter from './SortFilter'
import TabButton, { TAB_BUTTON_TYPE } from '../LeftFilter/Tabs/TabButton.js'

const Wrapper = styled(Flex)`
  margin: 5px 0px;
  flex-direction: column;

  position: relative;
  width: 100%;
`
const ResultsText = styled(Text)`
  text-align: center;
  min-width: 126px;
  font-style: normal;
  font-weight: 500;
  font-size: 1.6rem;
  margin-right: 14px;
  color: #e0e0e0;
`

const TopFilter = styled(Flex)`
  position: relative;
  flex-wrap: wrap;
  justify-content: flex-end;
  align-items: center;
  width: 100%;

  @media (max-width: 1000px) {
    justify-content: center;
  }
`
const ListsFilter = styled(Flex)`
  position: relative;
  flex-wrap: wrap;
  width: 100%;
`

const Title = styled(Text)`
  margin: auto 15px;
  font-style: normal;
  font-weight: 500;
  font-size: 1.6rem;
  // line-height: 23px;
  /* identical to box height */
  align-items: center;
  color: #e0e0e0;
  z-index: 99;
`
const CloseImage = styled.img`
  width: 39px;
  height: 39px;
  padding: 10px 20px 10px 0px;
  z-index: 99;
`
const ButtonText = styled.button`
  margin-top: 10px;
  height: 39px;
  color: #e0e0e0;
  background-color: Transparent;
  background-repeat: no-repeat;
  border: none;
  cursor: pointer;
  overflow: hidden;

  display: flex;
  justify-content: center;
  align-items: center;
  position: relative;
`
const ButtonClear = styled(Title)`
  color: #b499ff;
`
const Type = {
  ClassFilter: 1,
  TraitFillter: 2,
  IdFilter: 3,
}
const ExploreFilterHeader = ({ setQuery, query }) => {
  const [searchId, setSearchId] = useState('')
  const traits = useSelector(selectTraitConfig)
  const [traitTypeConfig, setTraitTypeConfig] = useState(TRAIT_TYPE.NAME)
  const types = useSelector(selectTypeConfig)

  const CLASS_TYPES = useMemo(() => {
    const typesConfig = get(types, 'cyblocTypes', [])
    return typesConfig
  }, [types])

  const { classType, traitType, traitId, cyblocId } = query

  const TRAITS_TYPES_CONFIG = useMemo(() => {
    return get(traits, 'traitTypes', [])
  }, [traits])

  const TRAITS_CONFIG = useMemo(() => {
    return get(traits, 'traits', [])
  }, [traits])
  const [mapListFilter, setMapListFilter] = useState([])

  const traitFilter = useMemo(() => {
    if (traitType && traitType.length !== 0) {
      setTraitTypeConfig(TRAIT_TYPE.NAME)
      return traitType
    }
    setTraitTypeConfig(TRAIT_TYPE.ID)
    return traitId
  }, [traitType, traitId])

  useEffect(() => {
    const class_filter = classType
      .map((classId) => {
        return find(CLASS_TYPES, (classObj) => {
          return Number(classId) === classObj.id
        })
      })
      .map((classFilter) => {
        return {
          ...classFilter,
          type: Type.ClassFilter,
        }
      })

    const trait_filter = traitFilter.map((id) => {
      const foundedName =
        traitTypeConfig === TRAIT_TYPE.NAME
          ? TRAITS_TYPES_CONFIG.find((el) => el.id === Number(id)).name
          : TRAITS_CONFIG.find((el) => el.id === Number(id)).name
      return { type: Type.TraitFillter, id, name: foundedName }
    })

    const id_filter = cyblocId
      ? [
          {
            type: Type.IdFilter,
            name: cyblocId,
          },
        ]
      : []

    const n_ListFilter = class_filter.concat(id_filter, trait_filter)
    setMapListFilter(n_ListFilter)
  }, [query])

  const onRemoveFilteredItem = (item) => {
    const { type } = item
    if (type === Type.ClassFilter) {
      const newClassFilter = classType.filter((classId) => Number(classId) !== item.id)
      setQuery({
        classType: newClassFilter,
        pageNumber: 1,
      })
    }

    if (type === Type.TraitFillter) {
      if (traitTypeConfig === TRAIT_TYPE.NAME) {
        setQuery({
          traitType: undefined,
          pageNumber: 1,
        })
      }

      if (traitTypeConfig === TRAIT_TYPE.ID) {
        setQuery({
          traitId: undefined,
          pageNumber: 1,
        })
      }
    }

    if (type === Type.IdFilter) {
      setSearchId('')
      setQuery({
        pageNumber: 1,
        cyblocId: undefined,
      })
    }

    const filteredList = mapListFilter.filter((_item) => _item.name !== item.name)
    setMapListFilter(filteredList)
  }

  const generateFilterType = (type) => {
    switch (type) {
      case Type.ClassFilter:
        return 'Class:'
      case Type.IdFilter:
        return 'Id:'
      case Type.TraitFillter:
        return 'Trait:'
      default:
        return null
    }
  }

  return (
    <Wrapper>
      {/* <TopFilter>
        {' '}
        <SortFilter setQuery={setQuery} />
      </TopFilter> */}
      <ListsFilter mt="10px">
        {mapListFilter.map((el) => (
          <TabButton mr="8px" mt="10px" type={TAB_BUTTON_TYPE.left} isActive>
            <Title>
              {generateFilterType(el.type)} {el.name}
            </Title>
            <CloseImage src="images/X.png" onClick={() => onRemoveFilteredItem(el)} />
          </TabButton>
        ))}
        {mapListFilter.length > 0 ? (
          <ButtonText
            onClick={() => {
              setQuery({
                classType: undefined,
                traitType: undefined,
                cyblocId: undefined,
                traitId: undefined,
                pageNumber: 1,
                shootingMin: undefined,
                shootingMax: undefined,
                crossingMin: undefined,
                crossingMax: undefined,
                tacklingMin: undefined,
                tacklingMax: undefined,
                passingMin: undefined,
                passingMax: undefined,
                dribblingMin: undefined,
                dribblingMax: undefined,
                physicalMin: undefined,
                physicalMax: undefined,
              })
            }}
          >
            <ButtonClear color="#B499FF" mt="30px">
              Clear all
            </ButtonClear>
          </ButtonText>
        ) : (
          <></>
        )}
      </ListsFilter>
    </Wrapper>
  )
}

export default ExploreFilterHeader
