import { useCallback } from 'react'
import { useWeb3React } from '@web3-react/core'
import { approveNftTokens } from 'utils/callHelpers'
import { useCyblocCoreContract, useCyblocMarketContract } from './useContract'

const useApproveTokens = (marketContractAddress, cyblocCoreAddress) => {
  const { account } = useWeb3React()
  const cyblocMarketContract = useCyblocMarketContract(marketContractAddress)
  const cyblocCoreContract = useCyblocCoreContract(cyblocCoreAddress)
  const handleApproveTokens = useCallback(
    async (tokenId) => {
      const tx = await approveNftTokens(marketContractAddress, tokenId, cyblocCoreContract, account)

      return tx
    },
    [account, cyblocMarketContract, cyblocCoreContract],
  )

  return { onApprove: handleApproveTokens }
}

export default useApproveTokens
