function waitFor(millSeconds) {
  return new Promise((resolve, reject) => {
    setTimeout(() => {
      resolve()
    }, millSeconds)
  })
}

export async function retryPromiseWithDelay(promise, nthTry, delayTime) {
  try {
    const data = await promise
    if (data.cyblocs.length === 0 || !data) {
      throw new Error()
    }
    return data
  } catch (e) {
    if (nthTry === 1) {
      return Promise.reject(e)
    }
    // wait for delayTime amount of time before calling this method again
    await waitFor(delayTime)
    return retryPromiseWithDelay(promise, nthTry - 1, delayTime)
  }
}
