import React, { useState, useMemo } from 'react'
import { useSelector } from 'react-redux'
import Flex from 'components/Flex'
import Text from 'components/Text'
import { Collapse } from 'react-collapse'
import get from 'lodash/get'
import styled from 'styled-components'
import { selectTypeConfig } from 'state/reducers/app'

import DropDownFilter from '../DropDownFilter'

const ClassFilterIcon = {
  Bronze_Sliver_Gold: '/images/ClassFilterBronze.png',
  Legendary: '/images/ClassFilterLegendary.png',
  Platinium: '/images/ClassFilterPlatinium.png',
}

const Content = styled(Flex)`
  padding: 45px 24px 20px 24px;
  flex-direction: column;
`

const Button = styled.button`
  position: relative;
  width: 100%;
  height: 48px;
  margin-top: 10px;
  justify-content: center;
  align-items: center;
  display: flex;
  background-color: transparent;
  border: none;
  cursor: pointer;
`

const FilterButton = styled(Button)`
  margin-top: 2rem;
  background-image: ${(props) =>
    props.selected ? "url('/images/BgFilterButtonSeleted.png')" : "url('/images/BgFilterButton.png')"};
  background-repeat: no-repeat;
  background-size: contain;
  background-position: center;
  // transition: background-image 0.3s ease-in, transform 0.1s ease-in, opacity: 0.3s ease-in;

  &:hover {
    // opacity: 1;
  }
  & > div {
    opacity: ${(props) => (props.selected ? '1' : '0.5')};
  }
  & > img {
    opacity: ${(props) => (props.selected ? '1' : '0.5')};
  }
  z-index: 99;
`

const IconImage = styled.img`
  width: 3rem;
  height: 3rem;
  // object-fit: contain;
  position: absolute;
  right: 12px;
  // top: 5px;
`
const Title = styled(Text)`
  font-size: 1.6rem;
  color: #e0e0e0;
  line-height: 17px;
  letter-spacing: 2px;
  font-weight: bold;
`

const ClassFilter = ({ setQuery, query }) => {
  const [isOpen, setIsOpen] = useState(window.screen.width > 1000)
  const types = useSelector(selectTypeConfig)
  const { classType } = query
  const CLASS_TYPES = useMemo(() => {
    const typesConfig = get(types, 'cyblocTypes', [])
    if (typesConfig && typesConfig.length !== 0) {
      return typesConfig.map((t) => {
        const { id } = t
        if (id === 1 || id === 2 || id === 3) {
          return {
            ...t,
            icon: ClassFilterIcon.Bronze_Sliver_Gold,
          }
        }

        if (id === 4) {
          return {
            ...t,
            icon: ClassFilterIcon.Platinium,
          }
        }

        if (id === 5) {
          return {
            ...t,
            icon: ClassFilterIcon.Legendary,
          }
        }
        return t
      })
    }
    return []
  }, [types])

  const onHandleSelectType = (classId) => {
    let newClassFilter
    const found = classType.some((id) => Number(id) === classId)
    if (found) {
      newClassFilter = classType.filter((id) => Number(id) !== classId)
    } else {
      newClassFilter = [...classType, classId]
    }

    setQuery({
      classType: newClassFilter,
      pageNumber: 1,
    })
  }

  const onChangeState = () => {
    setIsOpen(!isOpen)
  }
  return (
    <DropDownFilter mt="30px" isOpen={isOpen} title="Class" onChangeState={onChangeState}>
      <Collapse isOpened={isOpen}>
        <Content>
          {CLASS_TYPES.map((cyblockClass) => (
            <FilterButton
              key={cyblockClass.id}
              selected={classType.some((id) => Number(id) === cyblockClass.id)}
              onClick={() => {
                onHandleSelectType(cyblockClass.id)
              }}
            >
              <Title>{cyblockClass.name}</Title>
              <IconImage src={cyblockClass.icon} />
            </FilterButton>
          ))}
        </Content>
      </Collapse>
    </DropDownFilter>
  )
}
export default ClassFilter
