import axios from 'axios'
import { NETWORK, API_CYBALL } from 'config/constant'
import qs from 'query-string'

// eslint-disable-next-line no-undef

export const useApi = () => {
  const getCyblocList = async (limit, from, cyblocId, classType) => {
    try {
      const formatedClassType = classType.map((t) => Number(t))
      const { data } = await axios.get(`${API_CYBALL}/gallery-cyblocs`, {
        headers: {
          'x-functions-key': process.env.REACT_APP_AZURE_CODE,
        },
        params: {
          limit,
          from,
          ...(cyblocId && { tokenId: cyblocId }),
          ...(formatedClassType && formatedClassType.length !== 0 && { class: JSON.stringify(formatedClassType) }),
        },
      })

      return data
    } catch (error) {
      console.log(error, 'Fail to fetch cybloc list')
      return null
    }
  }

  return {
    getCyblocList,
  }
}
